	module.exports = {"data":{"markdownRemark":{"html":"<p>Congratulations on deciding to use HackerOne as your platform in submitting vulnerabilities! Here are the steps that'll get you up and hacking: </p>\n<ol>\n<li>Create an account <a href=\"https://hackerone.com/users/sign_up\">here</a>. <br>You don't have to use your real first and last name in creating an account. We understand that some hackers want to remain anonymouns and not disclose their real identity. You're free to use a pseudonym of your choice to keep your identity from being dislosed. </li>\n<li>Verify your email by clicking <strong>Confirm email address</strong> in the verification email sent to your personal email.  </li>\n<li>Configure your profile and account settings under <strong>Settings</strong>. You can customize these settings:</li>\n</ol>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Profile</td>\n<td>Add a picture to your profile, change your username, add a website, your location, and a personal blurb about yourself.</td>\n</tr>\n<tr>\n<td>Authentication</td>\n<td>Set up two-factor authentication.</td>\n</tr>\n</tbody>\n</table>","frontmatter":{"title":"Hacker Start-Up Guide","id":"hackers/hacker-start-up-guide","path":"/hackers/hacker-start-up-guide.html"}}},"pathContext":{}}

